.resume{
    overflow-x: hidden;
    .menu-icon{
        position: fixed;
        right: 36px;
        top:45px;
        padding:0px;
        z-index: 10;
        .fa{
            font-size: 2rem;
            &:hover{
                color: white;
            }
        }
    }
    .menu{
        display: none;
    }
    .content-area{
        background: #ebeae7;
        margin-left: 22%;
        width: 80%;
        height: 100vh;
        overflow-x: hidden;
        .scroll-section{
            overflow-y: scroll;
            overflow-x: hidden;
            padding-left: 50px;
            h1{
                padding-bottom:10px ;
                padding-left: 0px;
                padding-top: 50px;
                font-weight:550;
                font-size: 2rem;
                color: blue;
                margin-bottom: 56;
                text-transform: uppercase;
                
            }
            h5{
                font-weight: 500;
                font-size: 1.2rem;
                text-transform: uppercase;
                color: blue;
            }
            .xp-section{
                display: grid;
                grid-template-columns: 1fr 1fr;
                grid-column-gap: 50px;
                grid-row-gap: 20px;
                max-width: 900px;
                
                .xp-box{
                    cursor: pointer;
                   
                    transition: all 0.5, ease-in-out;
                    &:hover{
                        -webkit-box-shadow: 0px 1px 13px 0px rgba(0,0,0,0.71);
                        -moz-box-shadow: 0px 1px 13px 0px rgba(0,0,0,0.71);
                        box-shadow: 0px 1px 13px 0px rgba(0,0,0,0.71);
                    }
                }
                a{
                    text-decoration: none;
                    color:#a9a9a9;
                }
                h5{
                    font-weight: 500;
                    font-size: 1.3rem;
                    margin-bottom: 1rem;
                }
                p{
                    
                    font-weight: 300;
                    font-size: 1rem;
                    margin-bottom: 1rem;
                }
            }
            
        }
    }
    
}
// Small devices (landscape phones, 576px and up)
@media(max-width: 767px){
    .resume{
        .menu-icon{
            top: 5px;
            right: 10px;
        }
        .info-section{  
            left: 10px;
            padding: 0;         
            top:5px;
            .icon-section{
                a{
                    color:#857171;
                }
            }
            .info-box{
                left: 0px;
                bottom: 5px;
                h4{
                    color: black;
                }
                p{
                    color:#857171;
                }
            }
        }
        
        .content-area{
            .scroll-section{
                h1{
                    font-size: 1.5rem;
                    
                }
               
                .xp-section{
                    display:block;
                    
                }
                
            }
        }
        
 }
}